export default{
  props: {
    author: Object,
  },
  computed: {
    isFollowe() {
      return Number(this.author.is_settled);
    },
    followBtnText() {
      if (this.author.is_settled !== '0') {
        return '';
      }
      return '';
    },
  },
  methods: {
    onClick() {
      this.$emit('on-click-item', this.author.user_id);
    },
    follow() {
      if (this.author.is_settled !== '0') {
        // 
        this.author.is_settled = '0';
      } else {
        // 
        this.author.is_settled = '1';
      }
    },
  },
};



