import { mapState } from 'vuex';
import MusicPlayer from './components/MusicPlayer';

export default {
  name: 'app',
  components: {
    MusicPlayer,
  },
  computed: {
    ...mapState({
      showMusicPlayer: state => state.one.showMusicPlayer,
    }),
  },
};



